.App {
  text-align: center;
}

input {
  font-size: 1em;
  width: 50px;
  margin: 10px 5px;
}

label {
  margin: 0px 10px;
}

select {
  font-size: 1em;
  margin: 0px 5px;
}

button {
  font-size: 1em;
  margin: 10px 5px;
}

table {
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  border-spacing: 0px;
  border-collapse: collapse;
  border: 2px solid black;
}

td {
  border: 1px solid black;
  width: 30px;
  height: 30px;
}

tr:nth-child(5n):not(:last-child) {
  border-bottom: 3px solid black;
}

td:nth-child(5n):not(:last-child) {
  border-right: 3px solid black;
}

.cross {
  background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' version='1.1' preserveAspectRatio='none' viewBox='0 0 100 100'><path d='M100 0 L0 100 ' stroke='black' stroke-width='3'/><path d='M0 0 L100 100 ' stroke='black' stroke-width='3'/></svg>");
}

.full {
  background-color: darkgreen;
}
